.first-section {
  position: relative;
  background: url("./web-bottle-1-bg@2x.jpg") no-repeat center/cover;
  -webkit-transition: all 0.45s ease-out;
  -moz-transition: all 0.45s ease-out;
  -ms-transition: all 0.45s ease-out;
  -o-transition: all 0.45s ease-out;
  transition: all 0.45s ease-out; }
  .first-section .first-section--txt {
    position: absolute;
    top: 10.6816%;
    right: 117px;
    z-index: 2; }
    .first-section .first-section--txt .first-section--txt__title {
      -webkit-transition: all 0.25s ease-out;
      -moz-transition: all 0.25s ease-out;
      -ms-transition: all 0.25s ease-out;
      -o-transition: all 0.25s ease-out;
      transition: all 0.25s ease-out;
      width: 31.1979vw;
      height: auto;
      display: inline-block;
      background: url("./web-bottle-1-smart-bottle@2x.png") no-repeat center/contain; }
      .first-section .first-section--txt .first-section--txt__title h1 {
        font-size: 7.5521vw;
        opacity: 0;
        color: #fff;
        font-weight: bold;
        letter-spacing: 4px;
        line-height: 0.95;
        text-align: left;
        z-index: 2;
        overflow: hidden; }
    .first-section .first-section--txt p {
      margin-top: 47px;
      font-size: 1.823vw;
      line-height: 1.44;
      text-align: right;
      color: #fff;
      text-shadow: 0 0 20px rgba(0, 0, 0, 0.2); }
  .first-section .first-section--bottle {
    position: absolute;
    top: 15.6663%;
    left: 50.2083%;
    width: 25.15625vw;
    height: 37.1353vw;
    min-width: 340px;
    min-height: 500px;
    background: url("./web-bottle-1-bottle@2x.png") no-repeat center/contain;
    overflow: hidden;
    text-indent: -9999px;
    -webkit-transition: all 0.25s ease-out;
    -moz-transition: all 0.25s ease-out;
    -ms-transition: all 0.25s ease-out;
    -o-transition: all 0.25s ease-out;
    transition: all 0.25s ease-out; }

@media only screen and (max-width: 640px) {
  .first-section {
    background: url("./mobile-bottle-1-bg@2x.jpg") no-repeat center/cover; }
    .first-section .first-section--bottle {
      top: initial;
      bottom: 30px;
      width: 204px;
      height: 300px;
      min-width: 0;
      min-height: 0;
      left: initial;
      right: 25px; }
  .first-section .first-section--txt {
    top: 60px;
    text-align: center;
    right: 24.5px; }
    .first-section .first-section--txt .first-section--txt__title {
      width: 246px;
      height: 100px;
      background-position: right;
      background-size: cover; }
    .first-section .first-section--txt p {
      font-size: 20px; } }

@media only screen and (max-width: 320px) {
  .first-section {
    background: url("./mobile-bottle-1-bg@2x.jpg") no-repeat center/cover; }
    .first-section .first-section--txt {
      top: 40px;
      text-align: center;
      right: 24px; }
      .first-section .first-section--txt .first-section--txt__title {
